//
// Grid system
// --------------------------------------------------


// Container widths
//
// Set the container width, and override it for fixed navbars in media queries.

.container {
  .container-fixed();

  @media (min-width @screen-xl-min) { // custom added for xl media query in custom-variables
    width: @container-xl;
  }
}

// Extra Large grid
// custom added for xl media query in custom-variables
@media (min-width: @screen-xl-min) {
  .make-grid(xl);
}