#intro {      
  background: #bfbfbf;
  background-image: url("../../images/white-room-portrait-overlay-brown.jpg");
  background-repeat: no-repeat; 
  // background-position: 57% 30px; position for brick portrait
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  /*do not put bg att fixed, will cause issue with resizing intro bg properly, less of an issue for svg in works*/
  min-height: 100%;
  width: 100%;
  @media (max-width: @screen-xs-max) {
    background-position: 38% 45px;
  }
  @media (min-width: @screen-sm-min) {
    background-position: 30% 45px;
  }
  @media (min-width: @screen-md-min) {
    background-position: 26% 45px;
  }
  @media (min-width: @screen-md-max) {
    background-position: left 45px;
  }
}

#work {
  background: #b2cae2;
  background-image: url("../../images/pattern-blue-purple.svg"); 
  background-repeat: no-repeat;
  background-position: 15% 0%;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  /*mobile doesn't support bg attachment fixed but desktop does*/
  background-attachment: fixed;
}

#about-me {
  background: #a4aeb9;
  background-image: url("../../images/circuit-board.svg");
  background-repeat: no-repeat;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  width: 100%;
}

#resume {
  background: #cedde9;
}

#footer {
  background: #cedde9;
}


