@media (min-width: @screen-sm-min) {
  .overlay-panel-close {
    position: absolute;
    top: 18px;
    right: 24px;
    height: 50px;
    width: 50px;
    /* image replacement */
    display: inline-block;
    overflow: hidden;
    text-indent: 100%;
    white-space: nowrap;
    border-radius: 25px;
    background-color: #59646D;
    box-shadow: 0 2px 3px rgba(0, 0, 0, 0.4);
  }
  .overlay-panel-close::before, 
  .overlay-panel-close::after {
    /* close icon created in CSS */
    position: absolute;
    top: 23px;
    left: 15.5px;
    height: 3px;
    width: 20px;
    background-color: #ffffff;
    /* this fixes a bug where pseudo elements are slighty off position */
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
  }
  .overlay-panel-close::before {
    -webkit-transform: rotate(45deg);
    -moz-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    -o-transform: rotate(45deg);
    transform: rotate(45deg);
  }
  .overlay-panel-close::after {
    -webkit-transform: rotate(-45deg);
    -moz-transform: rotate(-45deg);
    -ms-transform: rotate(-45deg);
    -o-transform: rotate(-45deg);
    transform: rotate(-45deg);
  }
}

.no-touch .overlay-panel-close:hover {
  background-color: darken(#59646D, 5%);
}
.no-touch .overlay-panel-close:hover::before, 
.no-touch .overlay-panel-close:hover::after {
  background-color: #ffffff;
  -webkit-transition-property: -webkit-transform;
  -moz-transition-property: -moz-transform;
  transition-property: transform;
  -webkit-transition-duration: 0.3s;
  -moz-transition-duration: 0.3s;
  transition-duration: 0.3s;
}
