//no-touch modernizr prevent mobile from getting hover styles
.no-touch .social-icon > img.animation-element:hover {
  transform: scale(1.08, 1.08); 
  -moz-box-shadow: 2px 3px rgba(0,0,0,0.3); 
  -webkit-box-shadow: 2px 3px rgba(0,0,0,0.3); 
  -o-box-shadow: 2px 3px rgba(0,0,0,0.3); 
  box-shadow: 0px 3px 8px rgba(0,0,0,0.4);
  border-radius:100px; 
}

.animation-element.rotate-clockwise {
  opacity: 0;
  -moz-transition: all 700ms ease-out;
  -webkit-transition: all 700ms ease-out;
  -o-transition: all 700ms ease-out;
  transition: all 700ms ease-out;
  -moz-transform: rotate(-50deg);
  -webkit-transform: rotate(-50deg);
  -o-transform: rotate(-50deg);
  -ms-transform: rotate(-50deg);
  transform: rotate(-50deg);
}

.animation-element.rotate-clockwise.in-view {
  opacity: 1;
  -moz-transform: rotate(0deg);
  -webkit-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  -ms-transform: rotate(0deg);
  transform: rotate(0deg);
}

.animation-element.rotate-counterclockwise {
  opacity: 0;
  -moz-transition: all 700ms ease-out;
  -webkit-transition: all 700ms ease-out;
  -o-transition: all 700ms ease-out;
  transition: all 700ms ease-out;
  -moz-transform: rotate(50deg);
  -webkit-transform: rotate(50deg);
  -o-transform: rotate(50deg);
  -ms-transform: rotate(50deg);
  transform: rotate(50deg);
}

.animation-element.rotate-counterclockwise.in-view {
  opacity: 1;
  -moz-transform: rotate(0deg);
  -webkit-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  -ms-transform: rotate(0deg);
  transform: rotate(0deg);
}

